.App {
  color: white;
  background-color: black;
  padding-top: 0.2em;
  font-size: 12px;
  line-height: 2em;
}

.App header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}

.App header .body {
  -ms-flex: 1 1;
      flex: 1 1;
}
.App header svg {
  cursor: pointer;
}
.App header svg:hover {
  fill: white;
}



